// import .
import React from "react";
import styled from "styled-components";
import { Grid, Button } from "../../../elements/Index";
import ApplyUserModal from "../../ApplyUserModal";

// ApplicantButton   .
const ApplicantButton = (props) => {
  return (
    <React.Fragment>
      <ContentMedia>
        {props.passedData?.projectStatus === "" && (
          <Grid>
            {props.isme === "user" && (
              <>
                <Button
                  common
                  width="120px"
                  isValue="apply"
                  _onClick={(e) => {
                    // console.log(e);
                    props.applyUserModalOpen(e.target.value);
                  }}
                  margin="auto 10px"
                  border="1px solid #554475"
                  borderRadius="50px"
                >
                  
                </Button>
              </>
            )}

            {props.isme === "applicant" && (
              <Button
                common
                width="120px"
                isValue="cancel"
                _onClick={(e) => {
                  props.applyUserModalOpen(e.target.value);
                }}
                width="120px"
              >
                
              </Button>
            )}
          </Grid>
        )}
        <ApplyUserModal
          applyUserModalOpen={props.applyUserModalOpen}
          applyUserModal={props.applyUserModal}
          setApplyUserModal={props.setApplyUserModal}
          applyValue={props.applyValue}
          postId={props.post_id}
          passdedMenber={props.passdedMenber}
          statusCheck={props.statusCheck}
        />
        {props.passedData?.projectStatus === "" &&
          props.passedUserStatus === "member" && (
            <Grid>
              {/* <Button
                common
                width="120px"
                isValue="memberLiked"
                _onClick={(e) => {
                  props.applyUserModalOpen(e.target.value);
                }}
                margin="auto 10px"
                border="1px solid #554475"
                borderRadius="50px"
              >
                
              </Button> */}
              {props.isme === "member" && (
                <Button
                  common
                  width="120px"
                  isValue="teamExit"
                  _onClick={(e) => {
                    props.applyUserModalOpen(e.target.value);
                  }}
                >
                  
                </Button>
              )}
            </Grid>
          )}
      </ContentMedia>
    </React.Fragment>
  );
};

const ContentMedia = styled.div`
  @media screen and (max-width: 360px) {
    width: 325px;
    margin: 0px 0px 60px;
  }
  @media screen and (max-width: 500px) {
    width: 325px;
    margin: 0px 0px 100px;
  }
`;

// export      .
export default ApplicantButton;
