const kakaologinMiddleware = (code, pageGo) => {
  return function (dispatch, getState, { history }) {
    let goPage = pageGo;
    // console.log(goPage);
    // console.log("  ", code);

    apis
      .kakaoLogin(code)
      .then((res) => {
        // console.log(res);
        if (res.data.msg == "    .") {
          // window.alert("  .");

          dispatch(
            firstUser({
              email: res.data.data.email,
              snsId: res.data.data.id,
            })
          );
          // history.push(goPage);
          history.push("/");
          return;
        }
        if (res.data.msg == " ") {
          let email = getState().user.isEmail;
          let userCookie = res.data.data.token;
          setCookie("ScopeUser", userCookie, 30);
          // const ACCESS_TOKEN = res.data.token;
          // localStorage.setItem("token", ACCESS_TOKEN);
          dispatch(
            setUser({
              email: res.data.data.mail,
              nickname: res.data.data.nickname,
              userId: res.data.data.userId,
              userPropensityType: res.data.data.userPropensityType,
            })
          );
          // if (email) {
          //   history.replace(`/mypage:${res.data.data.userId}`);
          //   Swal.fire(
          //     "  .   ?",
          //     "",
          //     "info"
          //   );
          // }
          // window.history.back();
          // history.push(goPage);
          history.push("/");
        }
      })
      .catch((err) => {
        // console.log(" ", err);
        // alert(" .");
        // history.replace("/"); //    
        history.push("/");
        Swal.fire(" !", "", "warning");
      });
  };
};

