/* eslint-disable */
import { createAction, handleActions } from "redux-actions";
import { produce } from "immer";
import { apis } from "../../lib/axios";
import Swal from "sweetalert2";
// 
const EDIT_POST = "EDIT_POST";
// 
const DELETE_POST = "DELETE_POST";
//  
const STATUS_POST = "STATUS_POST";

//    
const editPost = createAction(EDIT_POST, (editcard) => ({ editcard }));
//    
const deletePost = createAction(DELETE_POST, (postId) => ({ postId }));
//    
const statusPost = createAction(STATUS_POST, (postId) => ({ postId }));

// 
const initialState = {
  list: [],
};

//  
export const editPostAPI = (post_id, editcard) => {
  return function (dispatch, getState, { history }) {
    apis
      .editPost(post_id, editcard)
      .then((res) => {
        history.goBack();
      })
      .catch((err) => {
        console.log(err.response);
        window.alert(err.response.data.msg);
      });
  };
};

//  
export const deletePostAPI = (postId) => {
  return function (dispatch, getState, { history }) {
    apis
      .deletePost(postId)
      .then((res) => {
        history.goBack();
      })
      .catch((err) => {
        console.log(err.response);
      });
  };
};

//  
export const statusPostAPI = (post_id, editstatus) => {
  return function (dispatch, getState, { history }) {
    apis
      .statusPost(post_id, editstatus)
      .then((res) => {
        // history.goBack();
      })
      .catch((err) => {
        console.log(err.response);
      });
  };
};

// 
const bookMarkAPI = (postId) => {
  return function (dispatch, getState, { history }) {
    apis
      .bookMarkChecked(postId)
      .then((res) => {
        console.log(res);
        // console.log(res.data.data.isBookmarkChecked);
        if (res.data.msg == "  ") {
          return Swal.fire("  !", "", "success");
        }
        if (res.data.msg == "  ") {
          return Swal.fire("   !", "", "success");
        }
      })
      .catch((err) => {
        console.log(err.response.data.msg);
        if (err.response.data.msg == "    .") {
          return Swal.fire("    !", "", "question");
        }
        if (err.response.data.msg == "    .") {
          return Swal.fire("    !", "", "question");
        }
        if (err.response.data.msg == "    .") {
          return Swal.fire(
            "     !",
            "",
            "warning"
          );
        }
        if (err.response.data.msg == "    .") {
          return Swal.fire(
            "    !",
            "",
            "warning"
          );
        }
        if (err.response.data.msg == "  .") {
          return Swal.fire(
            "    !",
            "",
            "warning"
          );
        }
      });
  };
};

// 
export default handleActions(
  {
    [EDIT_POST]: (state, action) =>
      produce(state, (draft) => {
        console.log(" ", action.payload);
      }),
    [DELETE_POST]: (state, action) =>
      produce(state, (draft) => {
        console.log(" ", action.payload);
      }),
    [STATUS_POST]: (state, action) =>
      produce(state, (draft) => {
        console.log("  ", action.payload);
      }),
  },
  initialState
);

const postDetailActions = {
  editPostAPI,
  deletePostAPI,
  statusPostAPI,
  bookMarkAPI,
};

export { postDetailActions };
