// import .
import React from "react";
import styled from "styled-components";
import { Grid, Text } from "../../../elements/Index";

// StackDetail   ..
const StackDetail = (props) => {
  return (
    <React.Fragment>
      <Grid display="flex" margin="20px auto">
        <Text margin="auto 10px auto 0px"></Text>
        <StackMedia>
          {props.passedData?.techStack.map((item, index) => {
            return (
              <Text margin="auto 5px" key={index}>
                <StackBox>{item}</StackBox>
              </Text>
            );
          })}
        </StackMedia>
      </Grid>
    </React.Fragment>
  );
};

// styled-components
const StackBox = styled.div`
  color: white;
  background-color: #554475;
  border: 1px solid #554475;
  border-radius: 10px;
  padding: 4px 10px;
  text-align: center;
`;

const StackMedia = styled.div`
  display: flex;
  @media screen and (max-width: 600px) {
    font-size: 10px;
    width: 120px;
  }
`;

// export      .
export default StackDetail;
