//     
const applyUserAPI = (postId) => {
  return function (dispatch, getState, { history }) {
    apis
      .applyUser(postId)
      .then((res) => {
        // console.log(res.data.data);
        // window.alert("    ");
        dispatch(applyUsers(res.data.data));
      })
      .catch((err) => {
        // console.log(err.response);
        window.alert("ERROR:   .");
      });
  };
};
