/* eslint-disable */
import { createBrowserHistory } from "history";
import thunk from "redux-thunk";
import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import { connectRouter } from "connected-react-router";
import User from "./modules/user";

import Post from "./modules/post";
import Stack from "./modules/stack";
import Card from "./modules/postadd";
import Sort from "./modules/sort";
import ReBook from "./modules/bookRecommend";
import Infinitys from "./modules/infinity";
import ApplyUser from "./modules/applyProject";
import Mypage from "./modules/myPage";
import pageCheck from "./modules/pageCheck";

export const history = createBrowserHistory();

const rootReducer = combineReducers({
  user: User,
  //   product: Product,
  //   comment : Comment,
  apply: ApplyUser,
  post: Post,
  stack: Stack,
  card: Card,
  sort: Sort,
  rebook: ReBook,
  infinity: Infinitys,
  mypage: Mypage,
  pagecheck: pageCheck,

  // 8.  history   .   history    .      .
  router: connectRouter(history),
});

const middlewares = [thunk.withExtraArgument({ history: history })];

// const env = process.env.NODE_ENV;

// if (env === "development") {
//   const { logger } = require("redux-logger");
//   middlewares.push(logger);
// }

// 4.  (redux devTools )
const composeEnhancers =
  //    window === "object"   . __REDUX_DEVTOOLS_EXTENSION_COMPOSE__     .
  typeof window === "object" && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        // Specify extensions options like name, actionsBlacklist, actionsCreators, serialize...
      })
    : compose;

// 5.  
// composeEnhancers  applyMiddleware     .
const enhancer = composeEnhancers(applyMiddleware(...middlewares));

let store = (initialStore) => createStore(rootReducer, enhancer);

export default store();
