// import .
import React from "react";
import styled from "styled-components";
import { Grid, Text } from "../../../elements/Index";
import DatePicker from "react-datepicker";
import { ko } from "date-fns/esm/locale";

// DateEdit   .
const DateEdit = (props) => {
  const startDate = (date) => {
    props.setStartdate(date);
    // if (date <= props.endDate) {
    //   props.setStartdate(date);
    // } else {
    //   window.alert("  .");
    // }
  };

  const endDate = (date) => {
    if (date > props.startDate) {
      props.setEnddate(date);
    } else {
      window.alert("  .");
    }
  };

  // const endDate = (data) => {
  //   props.setEnddate(data);
  // };
  return (
    <React.Fragment>
      <DateMedia>
        <Grid>
          <Text></Text>
          <Grid display="flex" textAlign="center" margin="20px auto">
            {/*  */}
            <Grid>
              <Text> </Text>
              <SDatePicker
                dateFormat="yyyy - MM - dd"
                selected={new Date(props.startDate)}
                onChange={startDate}
                startdate={props.startDate}
                selectsStart
                locale={ko}
                minDate={new Date()}
                placeholderText="  "
              />
            </Grid>
            {/*  */}
            <Grid>
              <Text> </Text>
              <SDatePicker
                dateFormat="yyyy - MM - dd"
                selected={new Date(props.endDate)}
                onChange={endDate}
                startdate={props.startDate}
                enddate={props.endDate}
                selectsEnd
                locale={ko}
                minDate={new Date()}
                placeholderText="  "
              />
            </Grid>
          </Grid>
        </Grid>
      </DateMedia>
    </React.Fragment>
  );
};

// styled-components
const SDatePicker = styled(DatePicker)`
  box-sizing: border-box;
  width: 96%;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #c4c4c4;
  color: black;
  font-size: 16px;
  text-align: center;
  margin-top: 0.6rem;
  margin-left: 8px;
  outline: none;
`;

const DateMedia = styled.div`
  @media screen and (max-width: 600px) {
    display: flex;
    width: 350px;
    height: 10px;
    margin-bottom: 40px;
    font-size: 10px;
  }
  @media screen and (max-width: 500px) {
    display: flex;
    width: 340px;
    height: 30px;
    margin-bottom: 55px;
  }
`;

// export      .
export default DateEdit;
